.simulator-main{
    position: relative;
}

.simulator-main canvas{
    position: absolute;
    bottom: 0;
}

.simulator-main::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(225,225,196,0.33);
    top: 0;
    left: 0;
    z-index: 1;
}